import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

export default class SceneInit {
  constructor(canvasID, camera, scene, stats, controls, renderer, fov = 36) {
    this.fov = fov;
    this.scene = scene;
    this.stats = stats;
    this.camera = camera;
    this.controls = controls;
    this.renderer = renderer;
    this.canvasID = canvasID;

  }

  initScene() {
    this.canvasContainer = document.getElementById("canvas-container");
    this.camera = new THREE.PerspectiveCamera(
      this.fov,
      this.canvasContainer.clientWidth / this.canvasContainer.clientHeight,
      1,
      1000
    );
    this.camera.position.z = 128;
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color( 0xffffff );

    // specify a canvas which is already created in the HTML file and tagged by an id
    // aliasing enabled
    const canvas = document.getElementById(this.canvasID);
    this.renderer = new THREE.WebGLRenderer({
      canvas,
      antialias: true,
    });

    this.renderer.setSize(
      this.canvasContainer.clientWidth,
      this.canvasContainer.clientHeight
    );
    document.body.appendChild(this.renderer.domElement);

    this.controls = new OrbitControls(this.camera, this.renderer.domElement);

    // ambient light which is for the whole scene
    let ambientLight = new THREE.AmbientLight(0xffffff, 0.7);
    ambientLight.castShadow = false;
    this.scene.add(ambientLight);

    // spot light which is illuminating the chart directly
    let spotLight = new THREE.SpotLight(0xffffff, 0.55);
    spotLight.castShadow = true;
    spotLight.position.set(0, 50, 10);
    this.scene.add(spotLight);

    //zoom
    this.controls.minDistance = 50;
    this.controls.maxDistance = 250;

    // if window resizes
    window.addEventListener("resize", () => this.onWindowResize(), false);
  }

  animate() {
    window.requestAnimationFrame(this.animate.bind(this));
    this.render();
    this.controls.update();
  }

  render() {
    this.renderer.render(this.scene, this.camera);
  }

  onWindowResize() {
    this.camera.aspect =
      this.canvasContainer.clientWidth / this.canvasContainer.clientHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(
      this.canvasContainer.clientWidth,
      this.canvasContainer.clientHeight
    );
  }
}
